.car {
    display: flex;
    flex-wrap: wrap;
}

.car-main-title {
    width: 100%;
    padding: 20px 0;
}

.car-info {
    width: calc(100%/3);
    background: #F1F1F1;
    border-radius: 8px;
    padding: 16px 24px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.6);
}

.car-info p {
    padding: 3px 0;
}

.car-info img {
    width: 100%;
}

.car-info p span:not(.uppercase) {
    color: #000000;
    padding-right: 5px;
}

.car .title {
    font-weight: 500;
    color: #000000;
    margin-bottom: 10px;
    text-align: left;
    width: 100%;
}

.uppercase {
    text-transform: uppercase;
}

.car-image-360 {
    width: calc(200%/3);
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

.car-image-360 img {
    width: 100%;
}

.icon-360 {
    width: 40px;
    height: 40px;
    background-size: 100%;
    background-position: center;
    background-repeat: no-repeat;
}

.warning-content {
    font-family: 'Noto Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    margin: 10px 0 20px 0;
}

.select-inside-outside {
    display: flex;
    column-gap: 30px;
    margin-top: 20px;
}

@media screen and (min-width: 1080px) {
    .warning-content {
        font-size: 14px;
    }
}

@media screen and (max-width: 860px) {
    .car-info {
        width: 100%;
        margin-bottom: 20px;
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
    }

    .car-info .title {
        width: 100%;
    }

    .col-half {
        width: calc(50% - 5px);
    }

    .car-image-360 {
        width: 100%;
        padding-left: 0px;
    }
}

@media screen and (max-width: 860px) {
    .col-half {
        width: 100%;
    }
}