.login-page {
    position: fixed;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-page form {
    display: inline-flex;
    flex-direction: column;
    box-shadow: 0 0 5px gray;
    padding: 20px;
    border-radius: 10px;
}

.login-page form input {
    font-size: 24px;
    margin: 10px 0;
    padding: 4px 8px;
}

.sign-in {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 36px;
    gap: 8px;

    /* Primary/01 */
    background: var(--primary);
    border-radius: 12px;

    /* Text */
    font-family: 'NotoSansRegular';
    font-style: normal;
    font-size: 15px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #FCFCFC;

    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.1563097s;
}

.sign-in:hover { background: var(--primary-hover); }

.sign-in>* {
    font-size: 1.2rem;
}
