.home {
    padding-top: 24px;
}

.main-title {
    font-family: 'NotoSansRegular';
    font-style: normal;
    font-weight: 600;
    font-size: 46px;
    line-height: 63px;
    color: #000000;
}

.row {
    margin-top: 48px;
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
}

.row>.item {
    width: calc(100%/4 - 3*32px/4);
    flex-grow: 0;
    cursor: pointer;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 0 4px #b9b7b7;
    border-radius: 12px;
    overflow: hidden;
}

.row>.item>img {
    width: 100%;
}

.row>.item .title {
    padding: 5px 0;
    font-family: 'NotoSansRegular';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 32px;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
    flex: none;
    order: 1;
    flex-grow: 0;
}

@media screen and (max-width: 1300px) {
    .main-title {
        font-size: 42px;
    }
    .row {
        margin-top: 36px;
        gap: 25px;
    }
    .row>.item {
        width: calc(100%/4 - 3*25px/4);
    }
}

@media screen and (max-width: 960px) {
    .main-title {
        font-size: 38px;
    }
    .row {
        margin-top: 32px;
        gap: 24px;
    }
    .row>.item {
        width: calc(100%/3 - 2*24px/3);
    }
    .row>.item>.title {
        font-size: 20px; 
    }
}

@media screen and (max-width: 720px) {
    .main-title {
        font-size: 36px;
        font-weight: 400;
    }
    .row {
        margin-top: 18px;
        gap: 20px;
    }
    .row>.item {
        width: calc(100%/2 - 20px/2);
    }
    .row>.item>.title {
        font-size: 18px; 
    }
}

@media screen and (max-width: 600px) {
    .row {
        gap: 15px;
    }
    .row>.item {
        width: calc(100%/2 - 15px/2);
    }
}

@media screen and (max-width: 340px) {
    .row>.item {
        width: 100%;
    }
}