.navbar {
    display: flex;
    justify-content: space-between;
}

.breadcrumb-menu {
    display: flex;
    align-items: center;
    column-gap: 8px;
    list-style: none;
    overflow-x: auto;
}

.breadcrumb-item {
    flex: none;
    order: 0;
    flex-grow: 0;
}

.breadcrumb-item a {
    font-family: 'NotoSansLight';
    font-style: normal;
    font-size: 12px;
    line-height: 24px;
    color: #000000;
    text-decoration: none;
    display: flex;
    align-items: center;
    column-gap: 8px;
}

.login-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 36px;
    gap: 8px;

    /* Primary/01 */
    background: var(--primary);
    border-radius: 12px;

    /* Text */
    font-family: 'NotoSansRegular';
    font-style: normal;
    font-size: 15px;
    line-height: 24px;
    letter-spacing: -0.01em;
    color: #FCFCFC;

    outline: none;
    border: none;
    cursor: pointer;
    transition: 0.1563097s;
}

.login-btn:hover { background: var(--primary-hover); }

.login-btn>* {
    font-size: 1.2rem;
}

@media screen and (max-width: 960px) {
    .login-btn {
        padding: 8px 32px;
    }
}

@media screen and (max-width: 720px) {
    .breadcrumb-menu,
    .breadcrumb-item a {
        column-gap: 6px;
    }
    .login-btn {
        padding: 8px 32px;
    }
}

@media screen and (max-width: 680px) {
    .login-btn {
        padding: 8px 24px;
    }
}

@media screen and (max-width: 650px) {
    .breadcrumb-menu,
    .breadcrumb-item a {
        column-gap: 4px;
    }
    .login-btn {
        padding: 8px 24px;
        margin-top: 24px;
    }
    .navbar {
        flex-direction: column;
    }
}

@media screen and (max-width: 460px) {
    .breadcrumb-menu,
    .breadcrumb-item a {
        column-gap: 0px;
    }
}