.cars .row .item .title {
    text-align-last: left;
    padding: 0 5px;
}

.cars .row .item .title.price-title {
    text-transform: capitalize;
}

.cars .row .item .title span {
    font-family: 'NotoSansLight';
}

@media screen and (max-width: 600px) {
    .cars .row .item .title {
        font-size: 14px;
        line-height: 20px;
    }
}