.overlay-loader {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    row-gap: 10px;
    position: fixed;
    background-color: #000000;
    z-index: 971563097;
    inset: 0;
}

.overlay-loader h3 {
    font-size: 1.2rem;
    color: #616161;
    user-select: none;
}

.spinner {
    font-size: 48px;
    position: relative;
    display: inline-block;
    width: 1em;
    height: 1em;
}

.spinner .spinner-blade {
    --duration: .6s;
    position: absolute;
    left: 0.4629em;
    bottom: 0;
    width: 0.074em;
    height: 0.2777em;
    border-radius: 0.0555em;
    background-color: #c1c1c1;
    -webkit-transform-origin: center -0.2222em;
    -ms-transform-origin: center -0.2222em;
    transform-origin: center -0.2222em;
    -webkit-animation: spinner-fade 1s infinite linear;
    animation: spinner-fade var(--duration) infinite linear;

    -webkit-animation-delay: calc((var(--i) - 1)*var(--duration)/12);
    animation-delay: calc((var(--i) - 1)*var(--duration)/12);

    -webkit-transform: rotate(calc((var(--i) - 1)*30deg));
    -ms-transform: rotate(calc((var(--i) - 1)*30deg));
    transform: rotate(calc((var(--i) - 1)*30deg));
}

.spinner .spinner-blade:nth-child(1) { --i: 1 }
.spinner .spinner-blade:nth-child(2) { --i: 2 }
.spinner .spinner-blade:nth-child(3) { --i: 3 }
.spinner .spinner-blade:nth-child(4) { --i: 4 }
.spinner .spinner-blade:nth-child(5) { --i: 5 }
.spinner .spinner-blade:nth-child(6) { --i: 6 }
.spinner .spinner-blade:nth-child(7) { --i: 7 }
.spinner .spinner-blade:nth-child(8) { --i: 8 }
.spinner .spinner-blade:nth-child(9) { --i: 9 }
.spinner .spinner-blade:nth-child(10){ --i: 10 }
.spinner .spinner-blade:nth-child(11){ --i: 11 }
.spinner .spinner-blade:nth-child(12){ --i: 12 }

@keyframes spinner-fade {
	0% {
	    background-color: #555555;
	}
	100% {
	    background-color: transparent;
	}
}